// Home section start
.home {
    height: 100%;
    padding-top: 10%;
    padding-bottom: 2%;
    background: url(../img/bg/background_1825.jpg) center (center/cover) no-repeat;
    &__logo {
        display: block;
        max-width: 100%;
        height: auto;
        margin: 0 auto;
    }
    &__title {
        text-align: center;
        font-size: 50px;
        line-height: 1.2;
        text-transform: uppercase;
        letter-spacing: 0.025em;
        color: #f1f1f1;
        font-weight: 100;
        margin-bottom: 110px;
        span {
            font-weight: 900;
        }
    }
}

// Home section end