.portfolio{
    background-color: black;
    padding-top: 5%;
    padding-bottom: 3%;
    &__title {
        text-align: center;
        font-size: 35px;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        color: #f1f1f1;
    }
    &__img_wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    &__img_item {
        width: 19%;
        height: 150px;
        background: burlywood;
        margin-bottom: 15px;
    }
}