.about {
    padding-top: 10%;
    padding-bottom: 3%;
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__img {
        width: 48%;
        height: 450px;
        background: gray;
    }
    &__text {
        width: 48%;
        margin-bottom: 80px;
        &_title {
            font-size: 35px;
            line-height: 80px;
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            color: #202020;
        }
        &_descr {
            font-size: 20px;
            font-weight: 300;
            letter-spacing: 0.025em;
            color: #202020;
        }
        &_subtitle {
            font-size: 16px;
            line-height: 50px;
            font-weight: bold;
            color: #202020;
        }
    }
    &__scroll {
        .scroll_text {
            color: #bebebe;
        }
    }
}